.block {
  border: 1px solid #c9c9c9;
  background-color: rgba(144, 144, 144, 0.075);
  margin: 0 auto;
  padding: 20px;
  margin-bottom: 30px;
}
.block-label {
  background-color: rgb(80, 80, 80);
  color: #fff;
  font-size: 25px;
  font-weight: 700;
  padding: 0 30px;
  margin-top: 40px;
}
.voice-block {
  display: flex;
}
.voice-block-img img {
  width: 300px;
  margin-left: 20px;
}
.voice-block-wav {
  padding-top: 100px;
}
