.main-inner {
  width: 100%;
  max-width: 68em;
  margin: 8em auto;
  padding: 0 2.5em;

}

.title {
  font-size: 48px;
  font-family: sans-serif;
  font-weight: bold;
}


.speech-label {
  color: rgb(80, 80, 80);
  font-weight: 500;
  padding: 0 30px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
